.chat {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
  border-bottom: 1px solid whitesmoke;
  cursor: pointer;
}

.chat__avatar {
  height: 35px !important;
  width: 35px !important;
}

.chat__readIcon {
  color: #ed3b55;
}

.chat__info {
  padding-left: 5px;
  flex: 1;
}

.chat__info > h4 {
  margin: 0px 0px !important;
  font-size: 12px;
  font-weight: 500;
}
.chat__info > p {
  font-size: 9px;
  margin: 0px;
}
